.spinner {
  background: transparent;
  border-radius: 50%;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  color: #FF5C00;
  font-size: 1.25em;
  height: 150px;
  left: 50%;
  letter-spacing: 4px;
  line-height: 150px;
  opacity: 0;
  position: absolute;
  text-align: center;
  text-shadow: 0 0 10px #FF5C00;
  text-transform: uppercase;
  top: 50%;
  transform: translate(-50%, -50%);
  transition: visibility 0.25s ease-in, opacity 0.25s ease-in;
  visibility: hidden;
  width: 150px;
}
.spinner::before {
  animation: spinner 2s linear infinite;
  border: 3px solid transparent;
  border-radius: 50%;
  border-right-color: 3px solid #FF5C00;
  border-top-color: #FF5C00;
  content: "";
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}
.spinner--active {
  opacity: 1;
  transition: visibility 0.2s ease-out, opacity 0.2s ease-out;
  visibility: visible;
}
.spinner__circle {
  animation: animate 2s linear infinite;
  background: transparent;
  display: block;
  height: 4px;
  left: 50%;
  position: absolute;
  top: calc(50% - 2px);
  transform-origin: left;
  width: 50%;
}
.spinner__circle::before {
  background: #FF5C00;
  border-radius: 50%;
  box-shadow: 0 0 20px #FF5C00;
  content: "";
  height: 16px;
  position: absolute;
  right: -6px;
  top: -6px;
  width: 16px;
}

@keyframes spinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes animate {
  0% {
    transform: rotate(45deg);
  }
  100% {
    transform: rotate(405deg);
  }
}